import React, { useEffect, useState } from "react";
import styled from "styled-components";
import Faq from "react-faq-component";

const data1 = {
  rows: [
    {
      title: "Lorem ipsum dolor sit amet,",
      content: `Lorem ipsum dolor sit amet, consectetur adipiscing elit. In sed tempor sem. Aenean vel turpis feugiat,
              ultricies metus at, consequat velit.`,
    },
    {
      title: "Nunc maximus, magna at ultricies elementum",
      content: `Nunc maximus, magna at ultricies elementum, risus turpis vulputate quam, vitae convallis ex tortor sed dolor.`,
    },
    {
      title: "Curabitur laoreet, mauris vel blandit fringilla",
      content: `Curabitur laoreet, mauris vel blandit fringilla, leo elit rhoncus nunc, ac sagittis leo elit vel lorem.
            Fusce tempor lacus ut libero posuere viverra.`,
    },
    {
      title: "What is the package version",
      content: `current version is 1.2.1`,
    },
  ],
};

const data2 = {
  rows: [
    {
      title: "Lorem ipsum dolor sit amet,",
      content: `Lorem ipsum dolor sit amet, consectetur adipiscing elit. In sed tempor sem. Aenean vel turpis feugiat,
              ultricies metus, consequat velit.`,
    },
    {
      title: "Nunc maximus, magna at ultricies elementum",
      content: `Nunc maximus, at ultricies elementum, risus turpis vulputate quam, vitae convallis ex tortor sed dolor.`,
    },
    {
      title: "Curabitur laoreet, mauris vel blandit fringilla",
      content: `Curabitur laoreet, mauris vel blandit fringilla, leo elit rhoncus nunc, ac sagittis leo elit vel lorem.
            Fusce tempor lacus ut libero posuere viverra.`,
    },
    {
      title: "What is the package version",
      content: `current version is 1.2.1`,
    },
  ],
};

const styles = {
  bgColor: "#18191a",
  titleTextColor: "black",
  rowTitleColor: "red",
  rowContentColor: "white",
  arrowColor: "white",
};

export const Raisebox = styled.div`
  min-height: 70vh;
  width: 90vw;
  margin: auto;
  background: #191919;
  box-shadow: 2vw 2vw 2vw 1vw rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  margin-top: 2%;
  @media only screen and (max-width: 360px) {
    margin-left: -11%;
  }
  @media only screen and (min-width: 361px) and (max-width: 480px) {
    margin-left: -10%;
  }
  @media only screen and (min-width: 481px) and (max-width: 768px) {
    margin-left: -5%;
  }
`;


export const Div1 = styled.div`
`;

export const Div2 = styled.div`
display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;
  padding:2%;
  text-align:left;
  @media only screen and (max-width: 786px) {
    display: grid;
    grid-template-columns: 1fr;
  }
`;

export const Heading = styled.h1`
font-weight: 900;
  letter-spacing: -0.07em;
  background: #fff;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  padding: 1rem;
  line-height: 0.8em;
  font-size: 5rem;
  margin-top: 10%;
  margin-left: 1%;
  @media only screen and (max-width: 360px) {
    font-size: 3rem;
    text-align: center;
    margin: 0 auto;
  }
  @media only screen and (min-width: 361px) and (max-width: 480px) {
    font-size: 4rem;
    text-align: center;
    margin: 0 auto;
  }
  @media only screen and (min-width: 481px) and (max-width: 767px) {
    font-size: 4.5rem;
    text-align: center;
    margin: 0 auto;
  }

  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    text-align: center;
    margin: 0 auto;
    margin-top: -5%;
  }

  @media only screen and (min-width: 1025px) and (max-width: 1200px) {
    font-size: 4rem;
    margin-top: 10%;
  }
`;

function home2() {
  return (
    <Raisebox>
        <Div1>
        <Heading>Have any questions?</Heading>
        </Div1>
        <Div2>
          <Faq data={data1} styles={styles} />
          <Faq data={data2} styles={styles} />
        </Div2>
    </Raisebox>
  );
}

export default home2;
